import axios from 'axios';
import Vue from 'vue';
import router from './router.js'
import vm from '../main.js'
//
axios.interceptors.request.use(function(config) {
  //config   axios(config)then()
  console.log('',vm.bLoading);
  vm.bLoading=true;
  console.log('2',vm.bLoading);
  // token
  let local = window.localStorage.getItem('user')
  let token = local ? JSON.parse(window.localStorage.getItem('user')).token : ''
  config.headers={
    token
  }
  return config;// 
}, function(error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
  vm.bLoading=false;
  console.log('',vm.bLoading);
  //response  ~~ axiosres
  //    res.status   res.data.err == 2
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//string
  if(response.data.err===2 && !currentRoute.includes('/login')){
    router.replace({
      path:'/login',
      query:{address:currentRoute}
    })
  }
  
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
window.axios=axios;   //
export default axios;